gsap.registerPlugin(ScrollTrigger);

function ScrollGorizont(){

    // const sectionRef = useRef(null);
    // const triggerRef = useRef(null);

    // useEffect(() => {
    //     const items = sectionRef.current;
    
    //     const savedPosition = sessionStorage.getItem('animationPosition');
    //     const initialPosition = savedPosition ? JSON.parse(savedPosition) : { translate: 0 };
    
    //     const pin = gsap.fromTo(items, initialPosition, {
    //         translate: "-300vw",
    //         ease: "none",
    //         duration: 1,
    //         delay:1,
    //         scrollTrigger: {
    //             trigger: triggerRef.current,
    //             start: "top top",
    //             end: "200% top",
    //             scrub: 1,
    //             pin: true,
    //             immediateRender: false,
    //             onUpdate: self => {
    //                 console.log(self.progress);
    //                 // Сохраняем текущую позицию анимации
    //                 sessionStorage.setItem('animationPosition', JSON.stringify({ translate: self.progress * -300 }));
    //             }
    //         }
    //     });
        
    //     return () => {
    //         pin.kill();
    //     };
    // }, []);

    const component = useRef();
    const slider = useRef();
  
    useLayoutEffect(() => {
      let ctx = gsap.context(() => {
        let panels = gsap.utils.toArray(".panel");
        gsap.to(panels, {
          xPercent: -100 * (panels.length - 1),
          ease: "none",
          scrollTrigger: {
            trigger: slider.current,
            pin: true,
            scrub: 1,
            snap: 1 / (panels.length - 1),
            end: () => "+=" + slider.current.offsetWidth
          }
        });
      }, component);
      return () => ctx.revert();
    });
    // const sectionRef_2 = useRef(null);
    // const triggerRef_2 = useRef(null);
    
    // useEffect(() => {
    //     const items = sectionRef_2.current;
    
    //     const savedPosition = sessionStorage.getItem('animationPosition');
    //     const initialPosition = savedPosition ? JSON.parse(savedPosition) : { translateX: 0 };
    
    //     const pin = gsap.fromTo(items, initialPosition, {
    //         translate: "-200vw",
    //         ease: "none",
    //         duration: 1,
    //         delay:1,
    //         scrollTrigger: {
    //             trigger: triggerRef_2.current,
    //             start: "top top",
    //             end: "200% top",
    //             scrub: 1,
    //             pin: true,
    //             immediateRender: false,
    //             onUpdate: self => {
    //                 console.log(self.progress);
    //                 // Сохраняем текущую позицию анимации
    //                 sessionStorage.setItem('animationPosition', JSON.stringify({ translate: self.progress * -300 }));
    //             }
    //         }
    //     });
    //     return () => {
    //         pin.kill();
    //     };
    // }, []);

   

    return(
        <section className="scrollGorizont__wrapper" id="services" >
            <div ref={component}>
                <div className="scrollGorizont__container__top" ref={slider}>
                    {[1, 2, 3, 4].map(sectionNumber => (
                        <Section key={sectionNumber} sectionNumber={sectionNumber} />
                    ))}
                </div>
            </div>
        </section>
    )
}
const Section = ({ sectionNumber }) => {
    const images = {
        1: [{src:balls, className:"scrollGorizont__image_1_3", alt:""},{src:avatar_1, className:"scrollGorizont__image_1_1", alt:""},{src:anketa, className:"scrollGorizont__image_1_2", alt:""}, {src:konfeta_1, className:"scrollGorizont__image_1_4", alt:""}, {src:konfeta_2, className:"scrollGorizont__image_1_5", alt:""}, {src:krujka_1, className:"scrollGorizont__image_1_6", alt:""}, {src:krujka_4, className:"scrollGorizont__image_1_7", alt:""}, {src:krujka_6, className:"scrollGorizont__image_1_8", alt:""}, {src:krujka_7, className:"scrollGorizont__image_1_9", alt:""}],
        2: [{src:balss, className:"scrollGorizont__image_2_8", alt:""}, {src:plan_1, className:"scrollGorizont__image_2_2", alt:""}, {src:plan_2, className:"scrollGorizont__image_2_3", alt:""}, {src:word, className:"scrollGorizont__image_2_4", alt:""}, {src:two, className:"scrollGorizont__image_2_5", alt:""}, {src:tochka, className:"scrollGorizont__image_2_6", alt:""}],
        3: [{src:balss, className:"scrollGorizont__image_1_3", alt:""}, {src:steny, className:"scrollGorizont__image_3_1", alt:""}, {src:divan, className:"scrollGorizont__image_3_2", alt:""}, {src:kreslo, className:"scrollGorizont__image_3_3", alt:""}, {src:kruslo, className:"scrollGorizont__image_3_4", alt:""}, {src:stolik, className:"scrollGorizont__image_3_5", alt:""}, {src:stolik_3, className:"scrollGorizont__image_3_6", alt:""}, {src:stolik_2, className:"scrollGorizont__image_3_7", alt:""}, {src:tochca, className:"scrollGorizont__image_3_8", alt:""}, {src:doggi, className:"scrollGorizont__image_3_9", alt:""}],
        4: [{src:balss, className:"scrollGorizont__image_1_1", alt:""}, {src:truby, className:"scrollGorizont__image_4_1", alt:""},{src:rosetka, className:"scrollGorizont__image_4_2", alt:""},{src:tochcca, className:"scrollGorizont__image_4_3", alt:""} ,{src:svetilnik_1, className:"scrollGorizont__image_4_4", alt:""},{src:svetilnik_2, className:"scrollGorizont__image_4_5", alt:""},{src:uslovRosetka_1, className:"scrollGorizont__image_4_6", alt:""}, {src:uslovRosetka_2, className:"scrollGorizont__image_4_7", alt:""},{src:kapli_1, className:"scrollGorizont__image_4_8", alt:""},{src:kapli_2, className:"scrollGorizont__image_4_9", alt:""},{src:kapli_3, className:"scrollGorizont__image_4_10", alt:""},{src:kapli_4, className:"scrollGorizont__image_4_11", alt:""}, {src:kapli_5, className:"scrollGorizont__image_4_12", alt:""},{src:kapli_6, className:"scrollGorizont__image_4_13", alt:""} ,{src:kapli_7, className:"scrollGorizont__image_4_14", alt:""} ]
    };

    const titles = {
        1: ["Как мы", "работаем?"],
        2: ["Далее", "планировка"],
        3: ["Далее", "концепция"],
        4: ["Далее", "пакет схем"],
    };
    const subtitles = {
        1: ["Мы работаем красиво", "и эффективно."],
        2: ["Расстановка мебели", "и оборудования"],
        3: ["Подбор материалов,","мебели, освещения"],
        4: ["Расположение всех элементов","с наименованиями и размерами"],
    };
    const process ={
        1:["Подготовительные", "работы"],
        2:["Планировочное решение"],
        3:["Концептуальное", "решение"],
        4:["Документация"],
    }
    const suptitle = {
        1:["- Обмер", "- Анкетирование", "- Творческое задание"],
        2:["- 3 варианта объемно-планировочнго решения с расстановкой мебели и оборудования"],
        3:["- Мудборды", "- Коллажи", "- 3D - визуализации"],
        4:["- Планы", "- Схемы", "- Развертки", "- Ведомости"],
    }
    const position = {
        1:"I этап.",
        2:"II этап.",
        3:"III этап.",
        4:"IV этап.",
    }
    const steps = {
        1: [
            "Согласовываем дату и время выезда на обмер в удобное для вас время. Подробно замеряем помещение, все вводы и выводы коммуникаций.",
            "",
            "Специально для вас мы создали очень подробную анкету, дающую объёмное представление о ваших потребностях, вкусовых предпочтениях.",
            "",
            "Творческое задание – по итогам анкетирования, мы составляем документ, объединяющий всю собранную ранее информацию в формате красивой и удобной презентации.",
        ],
        2: [
            "На данном этапе мы продумываем оптимальное зонирование, функциональность, эргономичность помещения.",
            "",
            "Предлагаем разнообразные варианты размещения мебели, чтобы у вас была возможность выбора наиболее удобной и оптимальной конфигурации пространства."
        ],
        3: [
            "3D - визуализации - объемное фотореалистичное изображение интерьера и помещения, наиболее подробно отражающее сочетание текстур и фактур материалов, мебели и декора: световые сценарии, цветовые решения. Позволяют понять, как будет выглядеть интерьер после завершения ремонтных работ.",
            "",
            "Также на данном этапе мы определяемся с бюджетом, материалами и уточняем стоимость основных позиций."
        ],
        4: [
            "- обмерный план",
            "- планировка",
            "- пакет схем монтажа/демонтажа",
            "- пакет схем расположения напольных покрытий/плинтусов/теплых полов",
            "- схем потолков",
            "- пакет схем расположения освещения/электрических выводов",
            "- схема расположения сантехники",
            "- развертки помещений с указанием расположения мебели/видов отделки/оборудования",
            "- эскизные чертежи заказной мебели",
            "- пакет ведомостей на отделочные материалы/сантехнику/освещение/мебель"
        ]
    };

    return (
        <div className={`scrollGorizont__section_${sectionNumber} _scrollGorizont__section_top panel`}>
            <div className={`scrollGorizont__images_avatar_${sectionNumber} _section__avatar`}>
                {images[sectionNumber].map((src, index) => (
                    <img key={index} className={`scrollGorizont__image_${sectionNumber}_${index}`} src={src.src} alt={`image_${sectionNumber}_${index}`} />
                ))}
            </div>
            <div className={`scrollGorizont__section_${sectionNumber}_text scrollGorizont__text`}>
                <div className="scrollGorizont__section_text_title__top">
                    <div className="scrollGorizont__section_text_title__top_sub">
                        <h4>{titles[sectionNumber].map((titles, index) => <React.Fragment key={index}> {(titles)}<br/> </React.Fragment>)}</h4>
                    </div>
                    <h3>{subtitles[sectionNumber].map((subtitles, index) => <React.Fragment key={index}> {subtitles}<br /></React.Fragment>)}</h3>
                </div>
                <div className="scrollGorizont__section_text_title__center">
                    <h5><span>{position[sectionNumber]}</span><br />{process[sectionNumber].map( (process, index) => <React.Fragment key={index}>{process}<br/> </React.Fragment>)}
                        <div className="title__center__container">{suptitle[sectionNumber].map((suptitle, index) => <React.Fragment key={index}>{suptitle}<br /></React.Fragment>)}</div>
                    </h5>
                </div>
                <div className="scrollGorizont__section_text_title__bottom">
                    <h5>{steps[sectionNumber].map( (step, index) => <React.Fragment key={index}>{step}<br/> </React.Fragment>)}</h5>
                </div>
            </div>
        </div>
    );
};


export default ScrollGorizont;


